<template>
  <div class="container flex-column">
    <div class="pic-top-bg" :style="{'background': `url(${storeSign.storeBgImg}) 0% 0% / 100% 5.96rem no-repeat`}">
      <div class="top-bg-masking"/>
      <div class="top-gradient"/>
    </div>
    <div class="nav-header flex-row">
      <div class="back-btn" @click="goBack" v-if="!$route.query.browerFirstIn"/>
      <div class="nav-title">店铺印象</div>
      <div class="share-btn" v-if="showShare" @click="doShare"/>
    </div>
    <div class="shop-row-bg flex-row">
      <img class="shop-avatar" :src="storeSign.logoUrl | imageToHttps"/>
      <div class="flex-column" style="margin-left: .16rem; position: relative; flex: 1;">
        <div class="store-name">
          {{storeSign.o2oStoreName || storeSign.storeName}}
        </div>
        <div class="flex-row">
          <div class="store-tag"><span>官方</span></div>
          <div class="fans">
            粉丝数{{shopBasicInfo.collectors | formatNumber}}
          </div>
        </div>
      </div>
      <div class="collection flex-row" @click="changeCollection">
        <div class="icon" v-if="!shopBasicInfo.isCollection"/>
        <span>{{shopBasicInfo.isCollection ? '已关注' : '关注'}}</span>
      </div>
    </div>
    <div class="shop-row-bg flex-column">
      <div class="row-title">店铺星级</div>
      <div class="flex-row" style="margin-top:.24rem">
        <div class="star-des">商品描述</div>
        <van-rate
          v-model="shopBasicInfo.pg"
          size=".24rem"
          gutter=".1rem"
          :count="5"
          color="#f0ba3a"
          void-icon="star"
          void-color="#e3e3e3"
          readonly
        />
        <div class="star-rate">{{shopBasicInfo.pg ? shopBasicInfo.pg.toFixed(1) : '0'}}</div>
      </div>
      <div class="flex-row" style="margin-top:.24rem">
        <div class="star-des">卖家服务</div>
        <van-rate
          v-model="shopBasicInfo.sg"
          size=".24rem"
          gutter=".1rem"
          :count="5"
          color="#f0ba3a"
          void-icon="star"
          void-color="#e3e3e3"
          readonly
        />
        <div class="star-rate">{{shopBasicInfo.sg ? shopBasicInfo.sg.toFixed(1) : '0'}}</div>
      </div>
      <div class="flex-row" style="margin-top:.24rem">
        <div class="star-des">物流服务</div>
        <van-rate
          v-model="shopBasicInfo.lg"
          size=".24rem"
          gutter=".1rem"
          :count="5"
          color="#f0ba3a"
          void-icon="star"
          void-color="#e3e3e3"
          readonly
        />
        <div class="star-rate">{{shopBasicInfo.lg ? shopBasicInfo.lg.toFixed(1) : '0'}}</div>
      </div>
    </div>
    <div class="shop-row-bg flex-row" @click="showShopEWMFlag = true">
      <div class="row-title">店铺二维码</div>
      <img class="icon-tag" :src="`${$cdnImageUrl}/shop/icon_shop_qr.png`"/>
    </div>
    <div class="shop-row-bg flex-row" @click="goCertification" v-if="$store.getters.getLoginStatus">
      <div class="row-title">店铺资质</div>
      <img class="icon-tag" :src="`${$cdnImageUrl}/shop/icon_shop_ certification_new.png`"/>
    </div>
    <!-- <div class="shop-row-bg flex-row">
      <div class="row-title">店铺地址</div>
      <div class="address-text">山东省青岛市崂山区青岛大学西</div>
    </div> -->
    <shop-ewm :shopBasicInfo="shopBasicInfo" @fnShowShopEWM="fnShowShopEWM" v-if="showShopEWMFlag"></shop-ewm>
  </div>
</template>
<script>
import { Rate } from 'vant';
import { mapGetters } from 'vuex'
import { manage, collectStore, getStoreSign } from '@/http/service/shop'
const shopEwm = () => import('../component/shop-ewm')
/** 【店铺印象】 */
export default {
  components: {
    [Rate.name]: Rate,
    shopEwm,
  },
  data() {
    return {
      rateList: [
        {
          name: '商品描述',
          rate: 4,
        },
        {
          name: '卖家服务',
          rate: 3,
        },
        {
          name: '物流服务',
          rate: 5,
        },
      ],
      shopBasicInfo: {},
      avatar: '',
      storeSign: {
        logoUrl: '',
        storeName: '',
        o2oStoreName: ''
      },
      showShopEWMFlag: false,
      showShare: this.$util.isSgRn() || this.$util.isHaierApp(),
    };
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  mounted() {
    this.getStoreSign();
  },
  methods: {
    goBack() {
      this.$store.dispatch('doHistoryBack');
    },
    shareLinkFactory() {
      let url = `${location.origin}/sgmobile/shopDetail`
      const query = this.getCleanQuerys()
      if (this.mId) {
        query.shareId = this.mId
      }
      if (this.storeSign.mdCode) {
        query.mdCode = this.storeSign.mdCode
      }
      const queryString = this.$util.parseParamsToQuery(query)
      return url + queryString
    },
    doShare() {
      const url = this.shareLinkFactory()
      console.log(url)
      this.$util.inAppShare({
        title: this.storeSign.o2oStoreName || this.storeSign.storeName,
        desc: this.$util.isSyBird() ? '定制智慧家 就找三翼鸟' : '海尔智家商城，海尔家电正品保障',
        url,
      })
    },
    getShopInfo() {
      manage({
        storeId: this.$route.query.storeId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        ionId: this.location.districtId,
        street: this.location.townshipId,
      }).then(res => {
        if (res.success) {
          this.shopBasicInfo = res.data;
          if ([4, 5, 6].includes(this.storeSign.storeType)) { // 品类、品旗、001 星级显示5星
            this.shopBasicInfo.lg = 5;
            this.shopBasicInfo.pg = 5;
            this.shopBasicInfo.sg = 5;
          }
          this.shopBasicInfo.storeName = this.storeSign.o2oStoreName || this.storeSign.storeName;
          this.shopBasicInfo.avatarImageFileId = this.storeSign.logoUrl;
        } else {
          this.$toast(res.message)
        }
      })
    },
    getStoreSign() {
      let longitude = ''
      let latitude = ''
      if (this.location) {
        longitude = this.location.longitude
        latitude = this.location.latitude
      }
      getStoreSign({
        storeId: this.$route.query.storeId,
        latitude,
        longitude,
        shareId: sessionStorage.getItem('shareUserId') || '',
        mdCode: sessionStorage.getItem('shareMdCode') || ''
      }).then(res => {
        if (res.success) {
          this.storeSign = res.data;
          this.getShopInfo();
          if (!res.data.logoUrl) {
            if (Number(res.data.synStoreType) !== 9 || this.$util.isSyBird()) {
              this.storeSign.logoUrl = `${this.$cdnImageUrl}/sybird_default_logo.png`;
            } else {
              this.storeSign.logoUrl = `${this.$cdnImageUrl}/uplus_logo.png`;
            }
          }
        }
      });
    },
    changeCollection() {
      // 判断登陆
      if (!this.$store.getters.getLoginStatus) {
        this.$store.dispatch('doLogin')
        return
      }
      collectStore({
        collectId: this.$route.query.storeId,
        type: 1,
      }).then(res => {
        if (res.success) {
          if (res.data) {
            this.shopBasicInfo.isCollection = res.totalCount === 1
            // 重新获取粉丝数
            this.getShopInfo()
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    fnShowShopEWM() {
      // 判断登陆
      // if (!this.$store.getters.getLoginStatus) {
      //   this.$store.dispatch('doLogin')
      //   return
      // }
      this.showShopEWMFlag = !this.showShopEWMFlag
    },
    goCertification() {
      this.$router.push({
        path: '/certification',
        query: {
          ownerId: this.shopBasicInfo.storeId,
        },
      })
    },
  },
  filters: {
    formatNumber(number) {
      if (!number) {
        return 0;
      } else {
        if (number >= 10000) {
          return `${(number / 10000).toFixed(2)}万`;
        } else {
          return number;
        }
      }
    }
  },
}
</script>
